import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	components:{
		uniPopup
	},
	props:{
		showDialog: {
			type: Boolean,
			default: false
		},
		type: {
			type: String,
			default: 'alert'
		},
		title: {
			type: String,
			default: ''
		},
		content: {
			type: String,
			default: ''
		},
	},
	watch: {
		showDialog(value){
			if(value){
				this.open()
			}
		}
	},
	methods:{
		open(){
			this.$refs.dialog.open()
		}
	}
}
